// -- Colors

// Primary

$c-orange-red: #ff6060;
$c-light-grey: #f7f7f7;
$c-primary: $c-orange-red;

// Secondary

$c-black: #000;
$c-white: #fff;

// Border-radius

$border-radius: 25px;
$card-radius: 10px;

// Linear-gradient 

$linear-gradient: linear-gradient(180deg, rgb(255 255 255 / 0%) 0%, rgb(0 0 0 / 50%) 100%);

// Breakpoints 

$desktop-up: 1200px;
$tablet-down: 768px;
$small-tablet-down: 550px;
$large-mobile-down: 500px;
$mobile-down: 400px;